//Avoid typekit FOUT
html {
  &.js {
    opacity: 0;
    transition: opacity .2s ease-in-out;

    &.wf-active,
    &.wf-inactive,
    &.ready {
      opacity: 1;
    }
  }
}

// SVG sprite
body > svg {
  display: none;
}

// RWD
img {
  max-width: 100%;
  height: auto;
}

html,
body {
  margin: 0;
  padding: 0;
  font-size: 0!important;
}
