html,
body {
  cursor: none;
  overflow: hidden;
  font-family: 'Heebo', sans-serif;
  font-weight: 900;
  max-width: 100vw;
}


.text-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

p {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 20px;
  color: #cacaca;
  line-height: 1.2;
  width: 100%;
  text-align: center;
  padding: 0 30px;
  pointer-events: none;
  user-select: none;
  margin: 0;
  visibility: hidden;
  will-change: transform;
  z-index: 50;

  @include mq(m) {
    font-size: 30px;
  }
}

.p2 {
  font-size: 200px;
  white-space: nowrap;
  opacity: 0.2;

  @include mq(m) {
    font-size: 400px;
  }
}
